import { Button } from "@/components/ui/Button";
import Link from "next/link";

export default function Home() {
    return (
        <div className="flex flex-col min-h-screen">
            <header className="container py-6 flex justify-between items-center">
                <h1 className="text-2xl font-bold text-[var(--primary)]">Manay Academy</h1>
                <nav>
                    <Link href="/login" className="text-sm font-medium hover:underline underline-offset-4">
                        Iniciar Sesión
                    </Link>
                </nav>
            </header>

            <main className="flex-1">
                <section className="py-24 text-center space-y-4 container">
                    <h2 className="text-4xl font-bold tracking-tighter sm:text-5xl md:text-6xl/none">
                        Bienestar y Crecimiento Integral
                    </h2>
                    <p className="mx-auto max-w-[700px] text-gray-500 md:text-xl dark:text-gray-400">
                        Únete a nuestra comunidad liderada por Ana Zepeda. Descubre herramientas para tu bienestar emocional y mental.
                    </p>
                    <div className="space-x-4 pt-4">
                        <Button>Ver Cursos</Button>
                        <Button variant="outline">Conocer Más</Button>
                    </div>
                </section>
            </main>

            <footer className="py-6 w-full shrink-0 items-center px-4 md:px-6 border-t">
                <p className="text-xs text-center text-gray-500 dark:text-gray-400">
                    © 2026 Manay Academy. Todos los derechos reservados.
                </p>
            </footer>
        </div>
    );
}
